    /*background-size: 2px 1px;*/
    /*background-color: orange;*/
    color: #8BD4C0;
    text-decoration: none;
}
#text {
    font-size: 20px;
    font-family: "Open Sans", sans-serif;
    font-weight:600;
    transition: 0.3s;
    color: #434343;
}
#text.active {
    color: #8BD4C0;
    font-weight: 700;
}
#text:hover {
    color: #8BD4C0;
}
.sticky-nav {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}
